getContext(\"2d\");\n      const total = this.chartData.data.reduce((a, b) => a + b, 0);\n\n      this.chart = new Chart(ctx, {\n        type: \"doughnut\",\n        data: {\n          labels: this.chartData.labels,\n          datasets: [\n            {\n              data: this.chartData.data,\n              backgroundColor: this.chartData.colors,\n              borderColor: \"#ffffff\",\n              borderWidth: 3,\n              hoverBorderWidth: 3,\n              hoverOffset: 4,\n            },\n          ],\n        },\n        options: {\n          responsive: true,\n          maintainAspectRatio: true,\n          cutout: \"58%\",\n          plugins: {\n            legend: {\n              display: false,\n            },\n            tooltip: {\n              backgroundColor: \"#1a1a2e\",\n              titleFont: { size: 13, weight: \"600\" },\n              bodyFont: { size: 12 },\n              padding: 10,\n              cornerRadius: 8,\n              callbacks: {\n                label: function (context) {\n                  const value = context.parsed;\n                  const percentage = Math.round((value / total) * 100);\n                  return ` ${context.label}: ${percentage}%`;\n                },\n              },\n            },\n          },\n          layout: {\n            padding: 10,\n          },\n        },\n        plugins: [\n          {\n            id: \"percentageLabels\",\n            afterDraw: (chart) => {\n              const { ctx: context, data: chartDataRef } = chart;\n              chart.getDatasetMeta(0).data.forEach((arc, i) => {\n                const value = chartDataRef.datasets[0].data[i];\n                if (value === 0) {\n                  return;\n                }\n                const percentage = Math.round((value / total) * 100);\n                const midAngle = (arc.startAngle + arc.endAngle) / 2;\n                const radius = (arc.outerRadius + arc.innerRadius) / 2;\n                const x = arc.x + Math.cos(midAngle) * radius;\n                const y = arc.y + Math.sin(midAngle) * radius;\n\n                context.save();\n                context.fillStyle = \"#ffffff\";\n                context.font =\n                  \"bold 12px -apple-system, BlinkMacSystemFont, sans-serif\";\n                context.textAlign = \"center\";\n                context.textBaseline = \"middle\";\n                context.fillText(`${percentage}%`, x, y);\n                context.restore();\n              });\n            },\n          },\n        ],\n      });\n    },\n  },\n};\n</script>\n\n<style scoped>\n.donut-chart {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: var(--spacing-lg, 24px);\n}\n\n.donut-chart canvas {\n  max-width: 220px;\n  max-height: 220px;\n}\n\n.donut-chart__empty {\n  width: 220px;\n  height: 220px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border: 2px dashed var(--color-border, #e5e7eb);\n  border-radius: 50%;\n}\n\n.donut-chart__empty-text {\n  font-size: 14px;\n  color: var(--color-text-secondary, #6b7280);\n  font-weight: 500;\n}\n\n.donut-chart__legend {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: var(--spacing-lg, 24px);\n  flex-wrap: wrap;\n}\n\n.donut-chart__legend-item {\n  display: flex;\n  align-items: center;\n  gap: 6px;\n}\n\n.donut-chart__legend-dot {\n  width: 10px;\n  height: 10px;\n  border-radius: 50%;\n  flex-shrink: 0;\n}\n\n.donut-chart__legend-label {\n  font-size: 13px;\n  color: var(--color-text-secondary, #6b7280);\n  font-weight: 400;\n}\n</style>\n"],"sourceRoot":""}]);
