}
}
@media (max-width: 640px) {
.safety-panel[data-v-1de42fbc] {
		padding: var(--spacing-md, 16px);
}
.safety-panel__stats-grid[data-v-1de42fbc] {
		grid-template-columns: 1fr;
}
}
