`, "",{"version":3,"sources":["webpack://./src/components/AreaChart.vue"],"names":[],"mappings":";AAsLA;EACA,kBAAA;EACA,WAAA;EACA,aAAA;AACA;AAEA;EACA,sBAAA;EACA,uBAAA;AACA","sourcesContent":["<template>\n  <div class=\"area-chart\">\n    <canvas ref=\"chartCanvas\" :width=\"width\" :height=\"height\"></canvas>\n  </div>\n</template>\n\n<script>\nimport {\n  Chart,\n  LineController,\n  LineElement,\n  PointElement,\n  Filler,\n  CategoryScale,\n  LinearScale,\n  Tooltip,\n} from \"chart.js\";\n\nChart.register(\n  LineController,\n  LineElement,\n  PointElement,\n  Filler,\n  CategoryScale,\n  LinearScale,\n  Tooltip,\n);\n\nexport default {\n  name: \"AreaChart\",\n  props: {\n    labels: {\n      type: Array,\n      required: true,\n    },\n    data: {\n      type: Array,\n      required: true,\n    },\n    width: {\n      type: Number,\n      default: 400,\n    },\n    height: {\n      type: Number,\n      default: 220,\n    },\n  },\n  data() {\n    return {\n      chart: null,\n    };\n  },\n  mounted() {\n    this.renderChart();\n  },\n  beforeDestroy() {\n    if (this.chart) {\n      this.chart.destroy();\n    }\n  },\n  watch: {\n    data() {\n      if (this.chart) {\n        this.chart.destroy();\n      }\n      this.renderChart();\n    },\n  },\n  methods: {\n    renderChart() {\n      const ctx = this.$refs.chartCanvas.getContext(\"2d\");\n\n      // Gradient fill\n      const gradient = ctx.createLinearGradient(0, 0, 0, this.height);\n      gradient.addColorStop(0, \"rgba(200, 120, 200, 0.35)\");\n      gradient.addColorStop(1, \"rgba(200, 120, 200, 0.02)\");\n\n      this.chart = new Chart(ctx, {\n        type: \"line\",\n        data: {\n          labels: this.labels,\n          datasets: [\n            {\n              data: this.data,\n              borderColor: \"#c878c8\",\n              backgroundColor: gradient,\n              borderWidth: 2.5,\n              pointBackgroundColor: \"#c878c8\",\n              pointBorderColor: \"#ffffff\",\n              pointBorderWidth: 2,\n              pointRadius: 4,\n              pointHoverRadius: 6,\n              fill: true,\n              tension: 0.35,\n            },\n          ],\n        },\n        options: {\n          responsive: true,\n          maintainAspectRatio: false,\n          plugins: {\n            legend: { display: false },\n            tooltip: {\n              backgroundColor: \"#1a1a2e\",\n              titleFont: { size: 13, weight: \"600\" },\n              bodyFont: { size: 12 },\n              padding: 10,\n              cornerRadius: 8,\n              callbacks: {\n                label: (context) => ` ${context.parsed.y} completed tasks`,\n              },\n            },\n          },\n          scales: {\n            x: {\n              grid: { display: false },\n              ticks: {\n                font: { size: 11 },\n                color: \"#9ca3af\",\n              },\n            },\n            y: {\n              beginAtZero: true,\n              grid: {\n                color: \"rgba(0,0,0,0.04)\",\n              },\n              ticks: {\n                font: { size: 11 },\n                color: \"#9ca3af\",\n                stepSize: 2,\n              },\n            },\n          },\n          layout: {\n            padding: { top: 20 },\n          },\n        },\n        plugins: [\n          {\n            id: \"peakLabel\",\n            afterDraw: (chart) => {\n              const dataset = chart.data.datasets[0];\n              const maxVal = Math.max(...dataset.data);\n              if (maxVal === 0) {\n                return;\n              }\n              const meta = chart.getDatasetMeta(0);\n              const maxIdx = dataset.data.indexOf(maxVal);\n              const point = meta.data[maxIdx];\n\n              if (point) {\n                const { ctx: c } = chart;\n                c.save();\n                const text = `${maxVal} completed tasks`;\n                c.font = \"11px -apple-system, BlinkMacSystemFont, sans-serif\";\n                const textWidth = c.measureText(text).width;\n                const boxPad = 6;\n                const boxX = point.x - textWidth / 2 - boxPad;\n                const boxY = point.y - 30;\n\n                c.fillStyle = \"rgba(26, 26, 46, 0.85)\";\n                c.beginPath();\n                c.roundRect(boxX, boxY, textWidth + boxPad * 2, 18, 4);\n                c.fill();\n\n                c.fillStyle = \"#ffffff\";\n                c.textAlign = \"center\";\n                c.textBaseline = \"middle\";\n                c.fillText(text, point.x, boxY + 9);\n                c.restore();\n              }\n            },\n          },\n        ],\n      });\n    },\n  },\n};\n</script>\n\n<style scoped>\n.area-chart {\n  position: relative;\n  width: 100%;\n  height: 220px;\n}\n\n.area-chart canvas {\n  width: 100% !important;\n  height: 100% !important;\n}\n</style>\n"],"sourceRoot":""}]);
